import React from 'react';
import Link from 'next/link';
import { FaHome } from 'react-icons/fa';
import Layout from '@/components/layout'; // Import Layout component

export default function SignupPage() {
  return (
    <Layout> {/* Wrap the content with the Layout component */}
      <div
        className="min-h-screen flex flex-col items-center justify-center bg-gray-900 text-white bg-cover bg-center"
        style={{ backgroundImage: 'url(/signup-bg.jpg)' }}
      >
        {/* aCloudBridge Button */}
        <Link
          href="/"
          className="absolute top-2 left-4 bg-[#126da7] text-white py-2 px-4 rounded-full flex items-center"
        >
          <FaHome className="text-white mr-2" />
          aCloudBridge
        </Link>

        <div className="bg-[#063970] p-8 rounded-lg shadow-md w-112">
          <h2 className="text-2xl font-bold mb-6 text-center">Sign Up</h2>
          <form>
            <div className="mb-4">
              <input
                type="text"
                className="w-full px-3 py-2 border rounded-md bg-[#a4fcfe] focus:outline-none focus:ring focus:ring-blue-500 text-[#1a4f6e]"
                placeholder="First Name"
              />
            </div>
            <div className="mb-4">
              <input
                type="text"
                className="w-full px-3 py-2 border rounded-md bg-[#a4fcfe] focus:outline-none focus:ring focus:ring-blue-500 text-[#1a4f6e]"
                placeholder="Last Name"
              />
            </div>
            <div className="mb-4">
              <input
                type="email"
                className="w-full px-3 py-2 border rounded-md bg-[#a4fcfe] focus:outline-none focus:ring focus:ring-blue-500 text-[#1a4f6e]"
                placeholder="Email Address"
              />
            </div>
            <div className="mb-4">
              <input
                type="password"
                className="w-full px-3 py-2 border rounded-md bg-[#a4fcfe] focus:outline-none focus:ring focus:ring-blue-500 text-[#1a4f6e]"
                placeholder="Password"
              />
            </div>
            <div className="mb-6">
              <input
                type="password"
                className="w-full px-3 py-2 border rounded-md bg-[#a4fcfe] focus:outline-none focus:ring focus:ring-blue-500 text-[#1a4f6e]"
                placeholder="Confirm Password"
              />
            </div>
            <button
              type="submit"
              className="w-full bg-[#67c1e2] text-white py-2 px-4 rounded-md"
            >
              SIGN UP
            </button>
          </form>
          <div className="mt-4 text-center">
            <p className="text-sm">
              Already have an account? <a href="/login" className="text-blue-500">LOGIN</a>
            </p>
          </div>
        </div>
      </div>
    </Layout>
  );
}